// _variables.scss
$primary-color: #007acc;
$secondary-color: #6c757d;
$success-color: #28a745;
$error-color: #dc3545;
$border-radius: 4px;

// Espa√ßamento e Fontes
$spacing-xxs: 4px;
$spacing-sm: 8px;
$spacing-md: 16px;
$font-size-sm: 14px;
$font-size-xs: 12px;
$font-size-base: 16px;

// üé® Cores e Textos
.text-success { color: $success-color; }
.text-error { color: $error-color; }
.text-sm { font-size: $font-size-sm; }
.text-xs { font-size: $font-size-xs; }
.font-bold { font-weight: bold; }
.font-medium { font-weight: 500; }

// üìè Espa√ßamento e Layout
.p-2 { padding: $spacing-sm; }
.p-1 { padding: $spacing-xxs; }
.mb-2 { margin-bottom: $spacing-sm; }
.mb-3 { margin-bottom: $spacing-md; }
.mt-1 { margin-top: $spacing-sm; }
.mt-3 { margin-top: $spacing-md; }
.mb-1 { margin-bottom: $spacing-xxs; }
.block { display: block; }
.w-full { width: 100%; }

// Componentes da Sidebar
.sidebar-container {
  h3 {
    color: #333;
  }
  
  .scroll {
    max-height: 400px;
    overflow-y: auto;
  }
  
  .mb-3 {
    label {
      font-size: 14px;
    }
    
    input {
      border: 1px solid #ccc;
      border-radius: $border-radius;
    }
  }

  .border { border: 1px solid #ccc; }
  .rounded { border-radius: $border-radius; }
  
  // Corrigindo o warning da divis√£o
  .space-y-2 > button {
      margin-bottom: calc($spacing-sm / 2); // Usando calc()
  }
  .space-y-2 > button:last-child {
      margin-bottom: 0;
  }
}

// Mixin para bot√µes
@mixin button-style($bg-color, $text-color: white) {
  background-color: $bg-color;
  color: $text-color;
  border: none;
  border-radius: $border-radius;
  padding: $spacing-sm $spacing-md;
  cursor: pointer;
  font-size: $font-size-base;
  
  &:hover {
    opacity: 0.9;
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

// Classes de bot√£o
.btn {
  @include button-style($primary-color);
  padding: 8px 12px;
  font-size: $font-size-sm;

  &.primary {
    @include button-style($primary-color);
  }
  
  &.secondary {
    @include button-style($secondary-color);
  }
  
  &.success {
    @include button-style($success-color);
  }
  
  &.error {
    @include button-style($error-color);
  }
}